import React from 'react';
import PropTypes from 'prop-types';
import { Modal, Button, Badge } from 'react-bootstrap';
import { formatDistance } from '../utils/formatters';

const StationModal = ({ show, onHide, station, onNavigate }) => (
    <Modal show={show} onHide={onHide}>
        <Modal.Header closeButton>
            <Modal.Title>{station.name}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <div className="mb-3">
                <strong>Location:</strong> {station.location}
            </div>
            {station.distance && (
                <div className="mb-3">
                    <strong>Distance:</strong>{' '}
                    <Badge bg="info">{formatDistance(station.distance)}</Badge>
                </div>
            )}
            <div className="mb-3">
                <strong>Queue:</strong>{' '}
                <Badge bg={station.current_queue > 5 ? 'danger' : 'success'}>
                    {station.current_queue} vehicles
                </Badge>
            </div>
            {station.operating_hours && (
                <div className="mb-3">
                    <strong>Operating Hours:</strong><br />
                    Opens: {station.operating_hours.open || 'Not specified'}<br />
                    Closes: {station.operating_hours.close || 'Not specified'}
                </div>
            )}
        </Modal.Body>
        <Modal.Footer>
            <Button variant="secondary" onClick={onHide}>
                Close
            </Button>
            <Button variant="success" onClick={onNavigate}>
                Navigate
            </Button>
        </Modal.Footer>
    </Modal>
);

StationModal.propTypes = {
    show: PropTypes.bool.isRequired,
    onHide: PropTypes.func.isRequired,
    station: PropTypes.shape({
        name: PropTypes.string.isRequired,
        location: PropTypes.string.isRequired,
        distance: PropTypes.number,
        current_queue: PropTypes.number,
        operating_hours: PropTypes.shape({
            open: PropTypes.string,
            close: PropTypes.string
        })
    }).isRequired,
    onNavigate: PropTypes.func.isRequired
};

export default StationModal;
