import React from 'react';
import PropTypes from 'prop-types';
import { Card, Button, ButtonGroup, Badge } from 'react-bootstrap';
import { formatDistance, formatWaitingTime, formatQueueLength } from '../utils/formatters';

/**
 * StationCard component displays individual station information
 */
const StationCard = ({ station, onViewDetails, onNavigate }) => (
    <Card className="mb-3 station-card">
        <Card.Body>
            <Card.Title>{station.name}</Card.Title>
            <Card.Text as="div">
                <div className="mb-2">
                    <strong>Location:</strong> {station.location}
                </div>
                {station.distance && (
                    <div className="mb-2">
                        <strong>Distance:</strong>{' '}
                        <Badge bg="info">{formatDistance(station.distance)}</Badge>
                    </div>
                )}
                <div className="mb-2">
                    <strong>Queue:</strong>{' '}
                    <Badge bg={station.current_queue > 5 ? 'danger' : 'success'}>
                        {station.current_queue} vehicles
                    </Badge>
                </div>
            </Card.Text>
            <ButtonGroup className="d-flex mt-3">
                <Button 
                    variant="primary" 
                    onClick={() => onViewDetails(station)}
                >
                    Details
                </Button>
                <Button 
                    variant="success"
                    onClick={() => onNavigate(station)}
                >
                    Navigate
                </Button>
            </ButtonGroup>
        </Card.Body>
    </Card>
);

StationCard.propTypes = {
    station: PropTypes.shape({
        name: PropTypes.string.isRequired,
        location: PropTypes.string.isRequired,
        distance: PropTypes.number,
        current_queue: PropTypes.number
    }).isRequired,
    onViewDetails: PropTypes.func.isRequired,
    onNavigate: PropTypes.func.isRequired
};

export default StationCard;
