import React from 'react';
import PropTypes from 'prop-types';
import StationCard from './StationCard';
import './StationList.css';

/**
 * StationList component displays a list of CNG stations
 */
const StationList = ({ stations, onStationSelect, onNavigate }) => (
    <div className="station-list">
        <h3 className="mb-4">Nearby CNG Stations</h3>
        <div className="station-list-content">
            {stations.length === 0 ? (
                <div className="text-muted text-center p-3">
                    Click the location button to find nearby stations
                </div>
            ) : (
                stations.map(station => (
                    <StationCard
                        key={station._id}
                        station={station}
                        onViewDetails={onStationSelect}
                        onNavigate={onNavigate}
                    />
                ))
            )}
        </div>
    </div>
);

StationList.propTypes = {
    stations: PropTypes.arrayOf(PropTypes.shape({
        _id: PropTypes.string.isRequired,
        name: PropTypes.string.isRequired,
        location: PropTypes.string.isRequired
    })).isRequired,
    onStationSelect: PropTypes.func.isRequired,
    onNavigate: PropTypes.func.isRequired
};

export default StationList;
