import React, { useState, useRef } from 'react';
import { Container, Row, Col, Alert } from 'react-bootstrap';
import Map from './components/map/Map';
import StationList from './components/StationList';
import StationModal from './components/StationModal';
import StationService from './services/stationService';
import './App.css';

function App() {
    const [selectedStation, setSelectedStation] = useState(null);
    const [showModal, setShowModal] = useState(false);
    const [stations, setStations] = useState([]);
    const [error, setError] = useState(null);
    const mapRef = useRef(null);

    const handleStationSelect = (station) => {
        setSelectedStation(station);
        setShowModal(true);
    };

    const handleNavigate = async (station) => {
        try {
            setError(null);
            if (mapRef.current) {
                await mapRef.current.getRoute(station);
            }
            setShowModal(false);
        } catch (err) {
            setError('Failed to calculate route. Please try again.');
            console.error('Navigation error:', err);
        }
    };

    const handleStationsUpdate = (newStations) => {
        setStations(newStations);
        setError(null);
    };

    const handleError = (message) => {
        setError(message);
    };

    return (
        <div className="App">
            <Container fluid>
                <header className="text-center my-4">
                    <h1>CNG Station Locator</h1>
                    <p>Find nearby CNG stations and check waiting times</p>
                </header>

                {error && (
                    <Alert variant="danger" onClose={() => setError(null)} dismissible>
                        {error}
                    </Alert>
                )}

                <Row>
                    <Col md={8}>
                        <div className="map-container">
                            <Map 
                                ref={mapRef}
                                onStationsUpdate={handleStationsUpdate}
                                onError={handleError}
                            />
                        </div>
                    </Col>
                    <Col md={4}>
                        <StationList 
                            stations={stations}
                            onStationSelect={handleStationSelect}
                            onNavigate={handleNavigate}
                        />
                    </Col>
                </Row>
            </Container>
            
            {selectedStation && (
                <StationModal
                    show={showModal}
                    onHide={() => setShowModal(false)}
                    station={selectedStation}
                    onNavigate={() => handleNavigate(selectedStation)}
                />
            )}
        </div>
    );
}

export default App;
