import React, { useEffect, useRef, forwardRef, useImperativeHandle } from 'react';
import PropTypes from 'prop-types';
import 'ol/ol.css';
import Map from 'ol/Map';
import View from 'ol/View';
import TileLayer from 'ol/layer/Tile';
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import OSM from 'ol/source/OSM';
import { fromLonLat } from 'ol/proj';
import { Point, LineString } from 'ol/geom';
import { Feature } from 'ol';
import { Style, Icon, Stroke } from 'ol/style';
import axios from 'axios';
import './Map.css';

const MapComponent = forwardRef(({ onStationsUpdate, onError }, ref) => {
    const mapRef = useRef();
    const mapInstance = useRef(null);
    const vectorSource = useRef(new VectorSource());
    const routeSource = useRef(new VectorSource());
    const userLocation = useRef(null);

    useEffect(() => {
        // Initialize map
        const map = new Map({
            target: mapRef.current,
            layers: [
                new TileLayer({ source: new OSM() }),
                new VectorLayer({
                    source: vectorSource.current,
                    style: (feature) => {
                        const type = feature.get('type');
                        return type === 'user' ? userStyle : stationStyle;
                    }
                }),
                new VectorLayer({
                    source: routeSource.current,
                    style: new Style({
                        stroke: new Stroke({
                            color: '#0066cc',
                            width: 4
                        })
                    }),
                    zIndex: 1
                })
            ],
            view: new View({
                center: fromLonLat([73.8567, 18.5204]), // Pune coordinates
                zoom: 12
            })
        });

        // Create location button
        const locationButton = document.createElement('button');
        locationButton.innerHTML = ' Find Nearby Stations';
        locationButton.className = 'location-button';
        locationButton.title = 'Find nearby CNG stations';

        // Add button to map
        mapRef.current.appendChild(locationButton);

        // Add click handler
        locationButton.addEventListener('click', handleLocationClick);

        mapInstance.current = map;

        return () => {
            // Cleanup
            locationButton.removeEventListener('click', handleLocationClick);
            map.setTarget(null);
        };
    }, []);

    const handleLocationClick = async () => {
        try {
            // Show loading state
            const button = document.querySelector('.location-button');
            if (button) {
                button.innerHTML = ' Searching...';
                button.disabled = true;
            }

            const position = await getCurrentPosition();
            userLocation.current = {
                latitude: position.coords.latitude,
                longitude: position.coords.longitude
            };

            // Update user marker
            updateUserMarker(userLocation.current);

            // Find nearby stations
            const stations = await findNearbyStations(
                userLocation.current.longitude,
                userLocation.current.latitude
            );

            if (stations.length === 0) {
                onError('No CNG stations found nearby. Try increasing the search radius.');
                return;
            }

            // Update station list
            onStationsUpdate(stations);

            // Center map to show all markers
            const extent = vectorSource.current.getExtent();
            mapInstance.current.getView().fit(extent, {
                padding: [50, 50, 50, 50],
                duration: 1000
            });

            // Reset button state
            if (button) {
                button.innerHTML = ' Find Nearby Stations';
                button.disabled = false;
            }
        } catch (error) {
            console.error('Location error:', error);
            onError(error.message || 'Could not get your location');
            
            // Reset button state
            const button = document.querySelector('.location-button');
            if (button) {
                button.innerHTML = ' Find Nearby Stations';
                button.disabled = false;
            }
        }
    };

    const getCurrentPosition = () => {
        return new Promise((resolve, reject) => {
            if (!navigator.geolocation) {
                reject(new Error('Geolocation not supported'));
                return;
            }
            navigator.geolocation.getCurrentPosition(resolve, reject);
        });
    };

    const findNearbyStations = async (longitude, latitude) => {
        try {
            const response = await axios.get('/api/stations/nearby', {
                params: { 
                    longitude, 
                    latitude,
                    radius: 5000 // 5km radius
                }
            });

            if (!response.data || !Array.isArray(response.data)) {
                throw new Error('Invalid response from server');
            }

            // Update station markers
            clearStationMarkers();
            response.data.forEach(addStationMarker);

            return response.data;
        } catch (error) {
            console.error('Error finding stations:', error);
            throw new Error(
                error.response?.data?.message || 
                'Failed to find nearby stations. Please try again.'
            );
        }
    };

    const updateUserMarker = ({ longitude, latitude }) => {
        // Remove existing user marker
        const userFeatures = vectorSource.current.getFeatures()
            .filter(f => f.get('type') === 'user');
        userFeatures.forEach(f => vectorSource.current.removeFeature(f));

        // Add new user marker
        const feature = new Feature({
            geometry: new Point(fromLonLat([longitude, latitude])),
            type: 'user'
        });
        vectorSource.current.addFeature(feature);
    };

    const addStationMarker = (station) => {
        const [lon, lat] = station.coordinates.coordinates;
        const feature = new Feature({
            geometry: new Point(fromLonLat([lon, lat])),
            type: 'station',
            properties: station
        });
        vectorSource.current.addFeature(feature);
    };

    const clearStationMarkers = () => {
        const stationFeatures = vectorSource.current.getFeatures()
            .filter(f => f.get('type') === 'station');
        stationFeatures.forEach(f => vectorSource.current.removeFeature(f));
    };

    const getRoute = async (station) => {
        if (!userLocation.current) {
            throw new Error('Please enable location first');
        }

        try {
            const response = await axios.post('/api/stations/route', {
                start: `${userLocation.current.latitude},${userLocation.current.longitude}`,
                end: `${station.coordinates.coordinates[1]},${station.coordinates.coordinates[0]}`
            });

            // Clear existing route
            routeSource.current.clear();

            // Draw new route
            const coordinates = response.data.coordinates.map(coord => fromLonLat(coord));
            const feature = new Feature({
                geometry: new LineString(coordinates)
            });
            routeSource.current.addFeature(feature);

            // Fit map to show route
            mapInstance.current.getView().fit(routeSource.current.getExtent(), {
                padding: [50, 50, 50, 50],
                duration: 1000
            });
        } catch (error) {
            console.error('Route error:', error);
            throw new Error('Failed to get route');
        }
    };

    useImperativeHandle(ref, () => ({
        getRoute
    }));

    return (
        <div ref={mapRef} className="map"></div>
    );
});

const userStyle = new Style({
    image: new Icon({
        anchor: [0.5, 1],
        src: 'data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="32" height="32"><circle cx="12" cy="12" r="8" fill="%230066cc" stroke="white" stroke-width="2"/></svg>',
        scale: 1
    })
});

const stationStyle = new Style({
    image: new Icon({
        anchor: [0.5, 1],
        src: 'data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="32" height="32"><path d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5c-1.38 0-2.5-1.12-2.5-2.5s1.12-2.5 2.5-2.5 2.5 1.12 2.5 2.5-1.12 2.5-2.5 2.5z" fill="%23e74c3c"/></svg>',
        scale: 1
    })
});

MapComponent.propTypes = {
    onStationsUpdate: PropTypes.func.isRequired,
    onError: PropTypes.func.isRequired
};

export default MapComponent;
